import axios from 'axios';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Category.css'


class Category extends Component {

  constructor(props) {

    super(props);
    this.state = {

      file: null,
      categoryName: null,
      people: [],
      id:null,
      loading: true,
      imageUrl: "",

    }
  }

  async componentDidMount() {
    const url = "http://localhost:8020/categorypost/categories";
    const response = await fetch(url);
    const data = await response.json();
    
    this.setState({ people: data.categoryposts,loading: false });
    this.setState({})
    this.searchArray = data
  }

  handleName(e) {
    let categoryName = e.target.value
    this.setState({ categoryName: categoryName })
  }

  handleFile(e) {
    let file = e.target.files[0]
    this.setState({ file: file })
  }

  handleUpload(e) {
    let file = this.state.file
    let categoryName = this.state.categoryName
    let formdata = new FormData()

    formdata.append('imageUrl', file)
    formdata.append('categoryName', categoryName)

    axios({
      url: `http://localhost:8020/categorypost/create`,
      method: "POST",
      headers: {
        authorization: `your token`
      },
      data: formdata
    }).then((res) => {
      this.componentDidMount()

    }, (err) => {
    }
    )

  }

  delete(id) {
    fetch('http://localhost:8020/categorypost/delete/' + id,
      {
        method: 'DELETE',
      }).then((data) => {
        data.json().then((resp) => {
          alert("Are You Sure Delete")
          this.componentDidMount()
        })
      })
  }

  renderTableData() {
    return this.state.people.map((data) => {
      const { _id, categoryName, imageUrl } = data
      return (

        <tr key={_id}>

          <td><div className="name">{categoryName}</div></td>

          <td><img className="img" src={imageUrl} /></td>

          <td><div className="button2">
            <Link to={{pathname:`/Menu/${data._id}`,state:{_id:this.state.id}}}>
              <button className="btn2">Add</button>
            </Link>
          </div>
          </td>

          <td>
            <div className="button3">
              <button className="btn3" onClick={() => this.delete(data._id)}> Delete </button>
            </div></td>

        </tr>
      )
    })
  }

  render() {

    const { id } = this.state;

    return (

      <div>



        <h1>Category</h1>

        <div className="container">

          <h4>Add Category</h4>

          <div className="file">
            <input type="file" multiple name="file" onChange={(e) => this.handleFile(e)} />
          </div>

          <div className="title">Title</div>
          <div className="text1">
            <input type="text" className="text2" multiple name="categoryName" onChange={(e) => this.handleName(e)} />
          </div>

          <div className="button1">
            <button className="btn1" onClick={(e) => this.handleUpload(e)}>Upload</button>
          </div>
        </div>

        <div className="container1">

          <h5>Categories</h5>

          <div className="content">

            <table id="table" >

              <tr>
                <th>Title</th>
                <th>Image</th>
                <th>Action</th>
              </tr>

            </table>

          </div>

          <div>

            <table id='students'>
              <tbody>
                {this.renderTableData()}
              </tbody>
            </table>
          </div>

        </div>
      </div>

    )
  }
}

export default Category;
